.loginPage {
  padding: 75px;
  position: relative;
  min-height: 100vh; }
  .loginPage > .container {
    padding-bottom: 100px; }
  .loginPage .input-group-append {
    max-width: 38%; }
  .loginPage .small-text {
    font-size: 12px;
    font-weight: bold;
    color: #7a7a7a; }
  .loginPage .logo {
    max-width: 350px; }
  .loginPage .intro-text {
    font-size: 38px;
    font-weight: bold;
    color: #111;
    line-height: 46px;
    margin-top: 80px;
    margin-bottom: 30px;
    max-width: 500px; }
  .loginPage .coins {
    max-width: 140px; }
  .loginPage .loginForm {
    box-shadow: 0 0 10px #bbb;
    padding: 30px;
    border-radius: 6px; }
    .loginPage .loginForm h1 {
      font-size: 28px;
      margin-bottom: 30px; }
  .loginPage .socialLogins {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap; }
  .loginPage .twitter-login-btn {
    width: 43px;
    height: 43px;
    width: auto;
    margin: 10px; }
  .loginPage .google-login-btn {
    width: 43px;
    height: 43px;
    background: url("../../assets/images/front/google-icon.svg") no-repeat center center;
    background-size: contain;
    border-radius: 6px;
    border: 0;
    margin: 10px; }
  .loginPage .google-login-btn * {
    display: none; }
  .loginPage .facebook-login-btn {
    width: 43px;
    margin: 10px;
    height: 43px;
    background: url("../../assets/images/front/fb-icon.svg") no-repeat center center;
    border-radius: 6px;
    background-size: contain; }
    .loginPage .facebook-login-btn i {
      display: none; }
  .loginPage .new {
    margin-top: 60px;
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    color: #7A91BA;
    margin-bottom: 2px; }
  .loginPage .join {
    display: block;
    font-weight: bold;
    font-size: 14px;
    color: #5832E0;
    text-align: center; }
  .loginPage .terms {
    font-size: 12px; }
    .loginPage .terms .text-blue {
      color: #5832E0; }
  @media screen and (max-width: 991px) {
    .loginPage .intro-text {
      font-size: 30px;
      margin-top: 50px; } }
  @media screen and (max-width: 767px) {
    .loginPage {
      padding: 30px; }
      .loginPage .logo {
        width: 90%; }
      .loginPage .intro-text {
        font-size: 140%;
        margin-top: 20px;
        line-height: 1.5;
        margin-bottom: 20px; }
      .loginPage .coins {
        display: none; }
      .loginPage .loginForm {
        padding: 0;
        box-shadow: none; } }
  .loginPage .react-dropdown-select {
    width: 100%;
    min-width: 130px; }

.warnin {
  margin-top: 20px;
  margin-bottom: 0;
  font-size: 13px;
  color: #d9875f; }
